body {
  font-family: "Source Sans Pro", sans-serif;
}

.video-icon {
  background: #335eea;
  width: 100px;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}
.video-icon .trangle-right {
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 25px solid transparent;
  border-left: 40px solid #fff;
  border-bottom: 25px solid transparent;
  position: relative;
  left: 5px;
}

.pluse-effect-sm {
  animation: pulse-sm 2s infinite;
}

.pluse-effect-xs {
  animation: pulse-xs 2s infinite;
}

@keyframes pulse-sm {
  0% {
    box-shadow: 0 0 0 0 #5383e1;
  }
  70% {
    box-shadow: 0 0 0 30px rgba(0, 0, 0, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}
@keyframes pulse-xs {
  0% {
    box-shadow: 0 0 0 0 #5383e1;
  }
  70% {
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}
.overlay:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.25);
}

section ul ul li {
  margin-bottom: 30px;
}

h2 {
  font-size: 40px;
}